.control-panel{display:flex;padding-top:10px;width:100%;font-family:Arial,"Helvetica Neue",sans-serif;font-style:normal;font-weight:normal;font-size:12px}.control-panel__top{display:flex;flex-wrap:wrap;max-width:130px;justify-content:center;box-sizing:border-box}.control-panel__main{width:210px;position:relative}.control-panel__small-btn{margin:0 60px 20px 0;position:relative}.control-panel__small-btn:nth-child(1)::before{content:"Сброс\a(R)";white-space:pre;position:absolute;top:101%;left:0;margin:auto;width:100%}.control-panel__small-btn:nth-child(2){margin-right:0}.control-panel__small-btn:nth-child(2)::before{content:"Старт/\aПауза\a(P)";white-space:pre;position:absolute;top:101%;left:0;margin:auto;width:100%}.control-panel__drop{position:relative}.control-panel__drop::before{content:"Уронить(ctrl)";white-space:pre;position:absolute;top:101%;left:0;margin:auto;width:100%}.control-panel__drop::after{content:"";position:absolute;top:50%;left:50%;background-position:center;background-repeat:no-repeat;background-size:cover;width:15px;height:15px;transform:translate(-50%, -50%);background-image:url("../../../images/arrow-x2.svg")}.control-panel__left{position:absolute;top:50%;left:30%;transform:translate(-50%, -50%)}.control-panel__left::before{content:"Влево";position:absolute;top:101%;left:-10%}.control-panel__left::after{content:"";position:absolute;top:50%;left:45%;background-position:center;background-repeat:no-repeat;background-size:cover;width:15px;height:15px;transform:translate(-50%, -50%);background-image:url("../../../images/arrow-left.svg")}.control-panel__right{position:absolute;top:50%;left:70%;transform:translate(-50%, -50%)}.control-panel__right::before{content:"Вправо";position:absolute;top:101%;left:30%}.control-panel__right::after{content:"";position:absolute;top:50%;left:55%;background-position:center;background-repeat:no-repeat;background-size:cover;width:15px;height:15px;transform:translate(-50%, -50%);background-image:url("../../../images/arrow-right.svg")}.control-panel__up{position:absolute;top:0;left:50%;transform:translate(-50%)}.control-panel__up::before{content:"Поворот";position:absolute;top:-1%;left:-90%}.control-panel__up::after{content:"";position:absolute;top:45%;left:50%;background-position:center;background-repeat:no-repeat;background-size:cover;width:15px;height:15px;transform:translate(-50%, -50%);background-image:url("../../../images/rotate.svg")}.control-panel__down{position:absolute;bottom:0;left:50%;transform:translate(-50%)}.control-panel__down::before{content:"Вниз";position:absolute;top:101%;left:25%}.control-panel__down::after{content:"";position:absolute;top:55%;left:50%;background-position:center;background-repeat:no-repeat;background-size:cover;width:15px;height:15px;transform:translate(-50%, -50%);background-image:url("../../../images/arrow-down.svg")}@media screen and (max-width: 768px){.control-panel{flex-direction:column-reverse}.control-panel__top{max-width:none;width:100%;margin-top:15px}.control-panel__main{width:100%;display:flex;justify-content:space-between;margin-top:20px}.control-panel__small-btn{margin:0 30px}.control-panel__small-btn:nth-child(1)::before{content:"Сброс";left:-3px}.control-panel__small-btn:nth-child(2){margin-right:auto}.control-panel__small-btn:nth-child(2)::before{content:"Старт/Пауза";left:-15px}.control-panel__drop::before{content:"Уронить"}.control-panel__left{position:relative;top:auto;left:auto;transform:translate(0, 0)}.control-panel__left::before{top:101%;left:0;margin:auto;width:100%}.control-panel__right{position:relative;top:auto;left:auto;transform:translate(0, 0)}.control-panel__right::before{top:101%;left:0;margin:auto;width:100%}.control-panel__up{position:relative;top:auto;left:auto;transform:translate(0, 0)}.control-panel__up::before{top:101%;left:0;margin:auto;width:100%}.control-panel__down{position:relative;top:auto;left:auto;transform:translate(0, 0)}.control-panel__down::before{top:101%;left:0;margin:auto;width:100%}}@media screen and (max-width: 410px){.control-panel{padding-top:7px;font-size:10px}}